.App {
    padding: 30px;
    display: flex;
}

.App > div {
    margin-right: 50px;
}

.todolist {
    background-color: #575870;
    margin-right: 50px;
    border: 2px solid rebeccapurple;
    border-radius: 5px;
    padding: 20px;
}

.todolist-empty {
    background-color: hotpink;
    border: 2px solid #575870;
    border-radius: 5px;
    padding: 20px;
}

.task {
    font-weight: bold;
}

.task-done {
    font-weight: normal;
    text-decoration: line-through;
}

.btn-active {
    background-color: cornflowerblue;
    color: white;
    border: 2px solid cornflowerblue;
    border-radius: 3px;
}

.input-error {
    outline: none;
    border: 2px solid hotpink;
}
